//@ts-nocheck
import { call, put } from "redux-saga/effects";

import Api from "../api";
import { ACCESS_TOKEN_NAME, REFRESH_TOKEN_NAME } from "../../Utils";
import { logoutUser } from "../reducers/authReducer";

function* callCheckingAuth(api, ...allRestParams) {
  const accessToken = localStorage.getItem(ACCESS_TOKEN_NAME); //  accessToken
  const response = yield call(api, accessToken, ...allRestParams); //    API,     ,        

  const code = response.status; //   
  if (code === 401) {
    //  401 -    access
    const { status: accessStatus } = yield call(Api.verifyToken, accessToken); //   ,   
    if (accessStatus === 401) {
      //  401 - accessToken 

      const refreshToken = localStorage.getItem(REFRESH_TOKEN_NAME); //  refreshToken
      const { status: refreshStatus } = yield call(
        Api.verifyToken,
        refreshToken
      ); //   ,   

      if (refreshStatus === 200) {
        // refreshToken 
        const { status, data, problem } = yield call(
          Api.refreshToken,
          refreshToken
        ); //   ,   
        if (status === 200) {
          //    
          const { access } = data;
          localStorage.setItem(ACCESS_TOKEN_NAME, access); //    localStorage
          const newResponse = yield call(api, access, ...allRestParams); //       ()
          return newResponse; //      
        } else {
          console.log(problem);
          // -     -       
          yield put(logoutUser());
        }
      } else {
        // refreshToken 
        yield put(logoutUser());
      }
    } else {
      return response;
    }
  } else {
    return response;
  }
}

export default callCheckingAuth;

